body{
  background-color: #10181B;
}

.App{
  background-color: #1B262C ;
  width: 750px;
  min-height:600px;
  margin: 50px auto;
}

#to-do-form input{

  background-color: white;
  border: 1;
  border-color: white;
  height: 50px;
  width: 400px;
  padding: 0 20px;
  margin: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  color: black;
  border-radius: 10px 0px 0px 10px;

}
#to-do-form input:focus{
  outline: none;

}

#to-do-form button{
  height: 50px;
  width: 250px;
  background-color: orange;
  color: black;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  border-radius: 0px 10px 10px 0px;
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;

}