.list{
color: white;
font-family: Arial, Helvetica, sans-serif;
font-size: 22px;
width: 500px;
height: 50px;
background-color: #10181B;
margin: 20px auto;
border-radius: 5px;

}

.list p{
    padding: 10px;
    position: relative;
}

.list p span{
    position: absolute;
    right: 20px;
    cursor: pointer;

}

.faicons{
   margin: 0 5px;
   font-size: 20px;
}